<template>
  <div class="mylogin">
    <van-form @submit="onSubmit">
      <van-field
        v-model="username"
        name="username"
        label="用户名"
        placeholder="用户名"
        :rules="[{ required: true, message: '请填写用户名' }]"
        autocomplete

      />
      <van-field
        v-model="password"
        type="password"
        name="password"
        label="密码"
        placeholder="密码"
        :rules="[{ required: true, message: '请填写密码' }]"
        autocomplete

      />
      <div style="margin: 20px;">
        <van-button round block type="danger" native-type="submit" class="btn">登入</van-button>
      </div>
    </van-form>
  </div>
</template>

<script>
import { Toast } from "vant";

export default {
  data() {
    return {
      username: "",
      password: ""
    };
  },
  methods: {
    onSubmit(params) {
      let _this = this;
      this.$axios.get("login", { params }).then(res => {
        console.log(res);
        // 请求数据成功后提示
        if (res.data == "200") {
          Toast({
            message: "登陆成功",
            onOpened() {
              // 登陆成功用户名存入vuex
              _this.$store.state.username = _this.username;
              _this.$store.state.login = 1;
            _this.$router.push({name:"user"})
            },
          });
        } else {
          Toast("登陆失败");
        }
      });
    }
  }
};
</script>

<style lang="less" scoped>
.mylogin {
  height: 200px;
  .btn {
    margin-top: 30px;
    margin-bottom: 60px;
  }
}
</style>